/**
 * First we will load all of this project's JavaScript dependencies which
 * includes Vue and other libraries. It is a great starting point when
 * building robust, powerful web applications using Vue and Laravel.
 */

import {auth} from "./modules/auth.module";

require('./bootstrap');
/**
 * The following block of code may be used to automatically register your
 * Vue components. It will recursively scan this directory for the Vue
 * components and automatically register them with their "basename".
 *
 * Eg. ./components/ExampleComponent.vue -> <example-component></example-component>
 */

import { createApp } from "vue";
import { createStore } from 'vuex';
import { createRouter, createWebHashHistory } from 'vue-router';
import routes from './router/router';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'nprogress/nprogress.css'
import NProgress from "vue-nprogress/src";;
import AuthService from "./services/auth.service";
import notify from '@kyvg/vue3-notification';
import LoginSfComponent from './components/Salesforce/LoginSfComponent';
const options = {
    latencyThreshold: 200, // Number of ms before progressbar starts showing, default: 100,
    router: true, // Show progressbar when navigating routes, default: true
    http: false // Show progressbar when doing Vue.http, default: true
};
const ngprocess = new NProgress(options);
const app = createApp({
    ngprocess
});
/**
 * Next, we will create a fresh Vue application instance and attach it to
 * the page. Then, you may begin adding components to this application
 * or customize the JavaScript scaffolding to fit your unique needs.
 */
export const store = createStore({
    state() {
        return {
            count: 0
        }
    },
    mutations: {
        increment(state) {
            state.count++
        }
    },
    actions: {},
    modules: {
        auth,
    },
});
const router = createRouter({
    history: createWebHashHistory(),
    routes, // short for routes: routes
    linkActiveClass: 'active',
    scrollBehavior: (to, from ,savedPosition) => {
        if (savedPosition) {
            return savedPosition;
        }
        if (to.hash) {
            return { selector: to.hash };
        }
        return { x: 0, y: 0 };
    }
});
router.afterEach(transition => {
    ngprocess.done();
});
router.beforeEach((to, from, next) => {
    const loggedIn = localStorage.getItem('user');
    // trying to access a restricted page + not logged in
    // redirect to login page
    ngprocess.start();
    if (to.meta.requiresAuth && !loggedIn) {
        next('/login');
    } else {
        if (to.meta.requiresAuth) {
            if (AuthService.checkPermission(to.path)) {
                next();
            } else {
                next("/permission-denied");
            }
        } else {
            next();
        }
    }
});
app.use(router);
app.mount('#app');
app.use(store);
app.use(notify)
app.component('LoginSf', LoginSfComponent);

// Add a response interceptor
axios.interceptors.response.use(function (response) {
    return response;
}, function (error) {
    if (error.response.status === 403) {
        AuthService.logout(router);
    }
    return Promise.reject(error);
});
