import LoginPage from "../pages/LoginPage";
import LayoutPage from "../pages/LayoutPage";
import DashboardComponent from "../components/DashboardComponent";
import ImportPlayersComponent from "../components/ImportPlayers/ImportPlayersComponent";
import UserListComponent from "../components/Users/UserListComponent";
const routes = [
        {
            path: '/login',
            component: LoginPage,
            meta: {
                requiresAuth: false,
                prettyName: "login"
            }
        },
        {
            path: '/reset-password/:hash',
            component: LoginPage,
            meta: {
                requiresAuth: false,
                prettyName : "reset-password"
            }
        },
        {
            path: '/forgot-password',
            component: LoginPage,
            meta: {
                requiresAuth: false,
                prettyName : "forgot-password"
            }
        },
        {
            path: '/logout',
            component: LoginPage,
            meta: {
                requiresAuth: false,
            }
        },
        {
            path: '/',
            component: LayoutPage,
            redirect : 'home',
            meta: {
                requiresAuth: true,
            },
            children : [
                {
                    path : "home",
                    component :  DashboardComponent,
                    name : "Dashboard",
                    meta : {
                        requiresAuth : true
                    },
            },
                {
                    path : "import-players",
                    component :  ImportPlayersComponent,
                    name : "Import Players",
                    meta : {
                        requiresAuth: true,
                    }
            },
                {
                    path : "user/user-list",
                    component :  UserListComponent,
                    name : "User list",
                    meta : {
                        requiresAuth: true,
                    }
            }
            ]
        },
        ]
        export default routes;
