<template>
    <UserListSearch :platforms="Platforms" @syncFromSalesforce="callBackSyncFromSalesforce"/>
    <div class="card">
        <div class="card-body">
            <div class="table-responsive">
                <table class="table table-hover table-bordered table-sm">
                    <thead >
                    <tr class="center">
                        <th>#</th>
                        <th>Name</th>
                        <th>Username</th>
                        <th>Platform</th>
                        <th class="text-right">Actions</th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="user in Users.data">
                        <td>{{ user.id }}</td>
                        <td>{{ user.name }}</td>
                        <td>{{ user.email }}</td>
                        <td>{{ user.platform }}</td>
                        <td class="text-right">
                            <div class="custom-btn-group">
                                <button class="btn btn-warning btn-sm">Edit</button>
                                <button class="btn btn-info btn-sm">Clone</button>
                                <button class="btn btn-danger btn-sm" v-if="user.platform !== 'salesforce'">Delete</button>
                                <button class="btn btn-danger btn-sm" v-if="user.platform === 'salesforce'">Safe Delete</button>
                            </div>
                        </td>
                    </tr>
                    </tbody>
                </table>
                <Pagination :data="Users" @pagination-change-page="fetchUsers" />
            </div>
        </div>
    </div>
</template>

<script>
import LaravelVuePagination from 'laravel-vue-pagination';
import Config from "../../config/config";
import UserListSearchComponent from "./UserListSearchComponent";
import {bus} from "../../app";
export default {
    name: "UserListComponent",
    components : {
        'Pagination': LaravelVuePagination,
        'UserListSearch' : UserListSearchComponent
    },
    data() {
        return  {
            Users : {},
            Platforms : {},
            Filters : {
                page : 1,
                max_page : 10,
                User : {
                    name : "",
                    email : "",
                }
            }
        }
    },
    methods :{
        //Fetch Products from DB
        fetchUsers(page = 1) {
            this.Filters.page = page;
            axios.post(Config.API_URL + 'user/user-list', this.Filters ).then((response) => {
                if(response.data.status){
                    this.Users = response.data.Users;
                    console.log(this.$refs.HeaderRef);
                    this.Platforms = [
                        {
                            id : 0, name : "Test1"
                        },
                        {
                            id : 1, name : "Test2"
                        }
                    ];
                }
            });
        },
        callBackSyncFromSalesforce(){
            axios.post(Config.API_URL + 'user/sync-from-salesforce', {} ).then((response) => {
                if(response.data.status){
                   this.fetchUsers();
                }
            });
        }
    },
    created() {
        this.fetchUsers();
    }
}
</script>

